import '../scssFile/Inpogress.css'
import React, { useEffect, memo } from "react";
import { useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import { useTranslation, Trans } from "react-i18next";

export const Completed = () => {
    const auth = useSelector(state => state.userStatus.auth)
    const history = useHistory();
    const { t } = useTranslation("translation",{useSuspense:false});
    useEffect(() => {
        if (!auth) history.push("/");
        return () => {
            //
        };
    }, [auth,history]);
    return (
        <div className="container-fluid">
            <div className="InProgressCourses">
                <div className="center">
                    <div className="Inprogress">
                        <section>
                            <header>
                                <div className="CourseHeader">
                                    <div className="header-content"><Trans t={t}>Cng ngh thng tin</Trans></div>
                                </div>
                            </header>
                            <div className="course-main-content">
                                <div className="course-main-content-center row">
                                    <div className="course-main-content-primary col-md-6 col-12">
                                        <a>
                                            <h1 className="course-main-content-title">
                                                Kha hc dy lp trnh t c bn n nng cao: HTML, CSS, Bootstrap
                                            </h1>
                                        </a>
                                        <span className="course-pill"><Trans t={t}> hon thnh</Trans></span>
                                    </div>
                                    <div className="course-main-content-secondary col-md-6 col-12">
                                        <div className="course-main-content-button">
                                            <div className="Course-link-button">
                                                <a href="Thongtinkhoahoc.html">
                                                    <span><Trans t={t}>n kha hc</Trans></span>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <footer className="Course-footer">
                                <div className="partner-name">
                                    <div> i hc CNTT v Truyn thng Vit-Hn</div>
                                </div>
                            </footer>
                        </section>
                    </div>
                </div>
            </div>

            <div className="InProgressCourses">
                <div className="center">
                    <div className="Inprogress">
                        <section>
                            <header>
                                <div className="CourseHeader">
                                    <div className="header-content"><Trans t={t}>Thit k  ha</Trans></div>
                                </div>
                            </header>
                            <div className="course-main-content">
                                <div className="course-main-content-center row">
                                    <div className="course-main-content-primary col-md-6 col-12">
                                        <a>
                                            <h1 className="course-main-content-title">S dng Adobe cho thit k  ha</h1>
                                        </a>
                                        <span className="course-pill"><Trans t={t}> hon thnh</Trans></span>
                                    </div>
                                    <div className="course-main-content-secondary col-md-6 col-12">
                                        <div className="course-main-content-button">
                                            <div className="Course-link-button">
                                                <a href="Thongtinkhoahoc.html">
                                                    <span><Trans t={t}>n kha hc</Trans></span>
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <footer className="Course-footer">
                                <div className="partner-name">
                                    <div>i hc Bch khoa  Nng</div>
                                </div>
                            </footer>
                        </section>
                    </div>
                </div>
            </div>
        </div>
    );
};
export default memo(Completed)